.body {
  font-family: "Questrial", sans-serif;
  font-size: 16px;
  line-height: 30px;
  margin: 0;
  text-align: left;
  background-color: #fff;
  font-weight: 400;
  color: #535967;
  padding: 0px 90px;
}
.team-item {
  width: 100%;
  text-align: center;
  transition: all 0.4s ease 0s;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 1.5rem !important;
}
.team-item:hover {
  box-shadow: 0 0 30px rgb(0 0 0 / 8%);
}
.team-info {
  position: relative;
  background: #fff;
  margin: -50px 20px 0px;
  padding: 20px 10px;
  border-radius: 5px;
  transition: all 0.4s ease 0s;
  overflow: hidden;
}
.team-item:hover .team-info {
  -webkit-transform: translateY(-20px);
  transform: translateY(-20px);
}
.team-info h5 {
  margin-bottom: 0px;
  font-size: 20px;
  font-family: "Kumbh Sans", sans-serif;
  font-weight: 700;
  line-height: 1.4;
  /* margin-bottom: 0.5rem; */
  color: #3f3a64;
}
.team-info p {
  margin-bottom: 10px;
  color: #535967;
  font-family: "Questrial", sans-serif;
  line-height: 30px;
  font-size: 16px;
}
.team-info .team-socials {
  transition: all 0.4s ease 0s;
  -webkit-transform: translateY(80px);
  transform: translateY(80px);
  padding-left: 0;
  list-style: none;
}
.team-item:hover .team-socials {
  opacity: 1;
  -webkit-transform: translateY(10px);
  transform: translateY(10px);
}
.list-inline-item1:not(:last-child) {
  margin-right: 0.5rem;
}
.list-inline-item1{
    display: inline-block;
}
.team-socials .list-inline-item1 .link-tag {
  width: 35px;
  height: 35px;
  text-align: center;
  border: 1px solid #ddd;
  display: inline-block;
  border-radius: 100%;
  color: #222;
  padding-top: 3px;
  font-size: 14px;
  background: #ddd;
  transition: all 0.3s ease-in-out;
}
@media screen and (max-width:480px) {
    .body{
        padding: 0 10px;
    }
}
