.inner-container{
    /* border: 2px solid green; */
    height: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    margin: 0px auto;
    margin-bottom: 90px;
}
.inner-container .sub-heading{
    padding-top: 100px;
    color: #26AB95;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    font-size: 17px;
}
.inner-container .heading1{
    /* border: 2px solid red; */
    width:55%;
    font-size: 40px;
    font-weight: 600;
    font-family: 'Poppins', sans-serif;
    color: #3F3A64;
    word-spacing: 5px;
    padding-top: 10px;
    text-align: center;
}
.inner-container .pera{
    /* border: 2px solid red; */
    width: 40%;
    font-size: 16px;
    font-family: 'Lato', sans-serif;
    color: #535967;
    padding-top: 20px;
    padding-bottom: 10px;
    text-align: center;
}
@media screen and (max-width:820px) {
    .inner-container .sub-heading {
        padding-top: 60px;
        font-size: 15px;
    }
    .inner-container .heading1 {
        width: 100%;
    }
    .inner-container .pera {
        width: 90% !important;
        padding-top: 20px;
        padding-bottom: 0px;
    }
}
@media screen and (max-width:480px) {
    .inner-container{
        /* border: 2px solid red; */
        height: auto;
        width: 100%;
        display: flex;
        flex-direction: column;
        /* justify-content: center; */
        align-items: center;
        margin-bottom: 90px;
    }
    .inner-container .sub-heading{
        padding-top: 40px;
        color: #26AB95;
        font-family: 'Poppins', sans-serif;
        font-weight: 500;
        font-size: 15px;
    }
    .inner-container .heading1{
        /* border: 2px solid red; */
        width:100%;
        padding: 0 30px;
        font-size: 2rem;
        color: #3F3A64;
        word-spacing: 5px;
        padding-top: 10px;
        text-align: center;
    }

    .inner-container .pera{
        /* border: 2px solid red; */
        width: 90% !important;
        font-size: 16px;
        font-family: 'Lato', sans-serif;
        color: #535967;
        padding-top: 20px;
        padding-bottom: 10px;
        text-align: center;
    }
}

@media screen and (max-width:375px) {
    .inner-container{
        /* border: 2px solid red; */
        height: auto;
        width: 100%;
        display: flex;
        flex-direction: column;
        /* justify-content: center; */
        align-items: center;
