        color: #26AB95;
        font-family: 'Poppins', sans-serif;
        font-weight: 500;
        font-size: 14px !important;
    }
    .inner-container .heading1{
        /* border: 2px solid red; */
        width:100%;
        padding: 0 30px;
        font-size: 1.5rem;
        color: #3F3A64;
        word-spacing: 5px;
        padding-top: 10px;
        text-align: center;
    }
    
    .inner-container .pera{
        /* border: 2px solid red; */
        width: 90% !important;
        font-size: 16px;
        font-family: 'Lato', sans-serif;
        color: #535967;
        padding-top: 20px;
        padding-bottom: 10px;
        text-align: center;
    }
}